import { renderSquircle } from "corner-smoothing";

let isMob = window.matchMedia("(max-width: 600px)").matches;

//     
function throttle(func, ms) {
  let isThrottled = false;
  let savedArgs = null;
  let savedThis = null;

  function wrapper() {
    if (isThrottled) {
      savedArgs = arguments;
      savedThis = this;
      return;
    }

    func.apply(this, arguments);
    isThrottled = true;

    setTimeout(function () {
      isThrottled = false;
      if (savedArgs) {
        wrapper.apply(savedThis, savedArgs);
        savedArgs = savedThis = null;
      }
    }, ms);
  }

  return wrapper;
}

function applyCornerSmoothing() {
  isMob = window.matchMedia("(max-width: 600px)").matches;

  document.querySelectorAll(".corner-smooth-40").forEach((el) => {
    //    testi__item-content
    if (el.classList.contains("testi__item-content")) {
      return;
    }
    renderSquircle(el, {
      cornerRadius: isMob ? 25 : 40,
      cornerSmoothing: 0.5,
    });
  });
  document.querySelectorAll(".corner-smooth-60").forEach((el) => {
    renderSquircle(el, {
      cornerRadius: isMob ? 40 : 60,
      cornerSmoothing: 0.5,
    });
  });
  document.querySelectorAll(".corner-smooth-60-smooth").forEach((el) => {
    renderSquircle(el, {
      cornerRadius: isMob ? 40 : 60,
      cornerSmoothing: 1,
    });
  });
  document.querySelectorAll(".corner-smooth-10").forEach((el) => {
    renderSquircle(el, {
      cornerRadius: 10,
      cornerSmoothing: 0.5,
    });
  });
}

//      .sub-menu
function applySubMenuSmoothing(submenu) {
  renderSquircle(submenu, {
    cornerRadius: 22,
    cornerSmoothing: 0.5,
  });
}

//      
applyCornerSmoothing();

//    DOM,     
const observer = new MutationObserver((mutations) => {
  let shouldApply = false;

  // ,        
  mutations.forEach((mutation) => {
    if (mutation.type === "childList") {
      mutation.addedNodes.forEach((node) => {
        if (node.nodeType === 1) {
          // ,   
          if (node.classList && node.classList.contains("corner-smooth-50")) {
            shouldApply = true;
          } else if (node.querySelectorAll) {
            // ,         
            const hasCornerSmoothElements =
              node.querySelectorAll(".corner-smooth-50").length > 0;
            if (hasCornerSmoothElements) {
              shouldApply = true;
            }
          }
        }
      });
    }
  });

  //        ,  
  if (shouldApply) {
    applyCornerSmoothing();
  }
});

//  observer     DOM
observer.observe(document.body, {
  childList: true,
  subtree: true,
});

//      (    200   )
const throttledResize = throttle(() => {
  applyCornerSmoothing();
}, 200);

//   
window.addEventListener("resize", throttledResize);

//        (  ,  )
window.addEventListener("load", () => {
  applyCornerSmoothing();
});

const buttons = document.querySelectorAll(".portfolio__label input");

if (buttons.length > 0) {
  buttons.forEach((btn) => {
    btn.addEventListener("change", (e) => {
      setTimeout(() => {
        applyCornerSmoothing();
      }, 0);
    });
  });
}
